<template>
	<v-row>
		<v-col>
			<v-sheet>
				<h2 class="text-center py-9 display-2 font-weight-bold">
					{{ this.$route.name }}
				</h2>
				
				
				<v-data-table
					v-if="items"
					:headers="headers"
					:items="items"
					class="elevation-1 table-row-pointer"
					@click:row="openDetails"
				>
	
				</v-data-table>
				
				<v-row
					justify="center"
					class="py-9"
  			>
					<v-btn
						depressed
						large
						:to="{name:'예약하기'}"
						class="mx-1"
						color="primary"
					>
						<v-icon left>
							mdi-pencil
						</v-icon>
						예약하기</v-btn>
					
				</v-row>
				

			</v-sheet>
		</v-col>
	</v-row>
</template>

<script>
import data from '@/assets/data/reserve.js';
export default {
  name: 'reserve',
  components: {
    
  },
	data () {
		return {
			headers:[
				{text:'No', value:'id', sortable:false, align: 'center', class:"blue-grey   white--text rounded-tl"},
				{text:'객실', value:'객실', sortable:false, align: 'center', class:"blue-grey   white--text"},
				{text:'입실일', value:'입퇴실일[0]', sortable:false, align: 'center', class:"blue-grey   white--text"},
				{text:'퇴실일', value:'입퇴실일[1]', sortable:false, align: 'center', class:"blue-grey   white--text"},
				{text:'예약자명', value:'예약자명', sortable:false, align: 'center', class:"blue-grey   rple white--text"},
				{text:'연락처', value:'연락처', sortable:false, align: 'center', class:"blue-grey   white--text rounded-tr"},
				
			],
			items: null
		}
	},
	created(){
		if(this.$dbBoo) this.dbConnect();
		else this.items = data;
	},
	methods: {
		openDetails(val){
			this.$router.push({
				name: "예약상세",
				query: {id:val.id}
			})
		},
		async dbConnect() {
			this.$db.collection('Reserve').get().then((consult)=>{
				this.items = consult.docs.map(v => {
					const item = v.data()
					return {
						id: item.id,
						객실: item.객실,
						입퇴실일: item.입퇴실일,
						예약자명: item.예약자명,
						연락처: item.연락처,
						추가인원: item.추가인원,
						수영장온수: item.수영장온수,
						비밀번호: item.비밀번호
					}
					
				})
				console.log(this.items)
			})
		},
	}
}
</script>

<style>
	.table-row-pointer tbody > tr :hover{cursor:pointer}
</style>