<template>
	<v-row>
		<SubMenu v-bind:items="items" />
			
		<v-col>
			<v-sheet
				min-height="70vh"
				rounded="lg"
			>
				<h2 class="text-center py-9 display-2 font-weight-bold">
					{{ this.$route.name }}
				</h2>
				
				<v-list three-line>
					<v-list-item-group
						style="border-top:1px solid #e1e1e1"
						active-class="pink--text"
						multiple
					>
						<template v-for="(item, index) in items">
							<v-list-item :key="index" class="py-3" :to="{ name: '객실안내 상세', params: {contentId: index+1} }" style="border-bottom:1px solid #e1e1e1">
								<v-img
									max-height="150"
									max-width="250"
									:src="item.src"
								></v-img>
								<v-list-item-content class="ml-5 py-5">
									<v-list-item-title class="title font-weight-bold">
										{{ item.이름 }}
									</v-list-item-title>
									<v-list-item-subtitle class="subheading">
										{{ item.유형 }}
									</v-list-item-subtitle>
								</v-list-item-content>
							</v-list-item>
						</template>
					</v-list-item-group>
				</v-list>

				
				
			</v-sheet>
		</v-col>
	</v-row>
</template>

<script>
import data from '@/assets/data/rooms.js';
import SubMenu from '@/components/inc/SubMenu.vue';
	
export default {
  name: 'about',
	components: {
		SubMenu
	},
	data () {
		return {
			items: null
		}
	},
	created(){
		if(this.$dbBoo) this.dbConnect();
		else this.items = data;
	},

	methods: {
		async dbConnect() {
			this.$db.collection('Rooms').get().then((consult)=>{
				this.items = consult.docs.map(v => {
					const item = v.data()
					return { 
						이름: item.이름,
						특이사항: item.특이사항,
						넓이: item.넓이,
						유형: item.유형,
						기준인원: item.기준인원,
						가격: item.가격,
						src: item.src,
					}
				})
			})

		}
	}
	
}
</script>