<template>
	<v-row>
		<v-col>
			<v-sheet
				min-height="70vh"
				rounded="lg"
			>
				<h2 class="text-center py-9 display-2 font-weight-bold">
					{{ this.$route.name }}
				</h2>
				
				<div id="map"></div>
				
			</v-sheet>
		</v-col>
	</v-row>
</template>

<script>
import data from '@/assets/data/rooms.js';
import SubMenu from '@/components/inc/SubMenu.vue';
	
export default {
  name: 'about',
	components: {
		SubMenu
	},
	data () {
		return {
			map: null,
		}
	},
	mounted(){
		if(!window.kakao || !window.kakao.maps){
			//script 태그 객체 생성
			const script = document.createElement("script");
			//src 속성을 추가하며 .env.local에 등록한 service 키 활용
			// 동적 로딩을 위해 autoload=false 추가
			script.src = `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${process.env.VUE_APP_KAKAOMAP_KEY}`;
			script.addEventListener("load", () => {
				kakao.maps.load(this.initMap)
			});
			document.head.appendChild(script);
		} else {
			this.initMap();
		}
	},

	methods: {
		initMap() {
			const container = document.getElementById("map");
			const options = {
				center: new kakao.maps.LatLng(37.5155423, 127.017060),
				level: 3,
			}
			this.map = new kakao.maps.Map(container, options);
			

			// 커스텀 오버레이에 표출될 내용으로 HTML 문자열이나 document element가 가능합니다
			var content = '<div class="customoverlay">' +
					'  <a href="https://map.kakao.com/link/map/11394059" target="_blank">' +
					'    <span class="title">정글 펜션</span>' +
					'  </a>' +
					'</div>';

			// 커스텀 오버레이가 표시될 위치입니다 
			var position = new kakao.maps.LatLng(37.5155423, 127.017060);  

			// 커스텀 오버레이를 생성합니다
			var customOverlay = new kakao.maps.CustomOverlay({
					map: this.map,
					position: position,
					content: content,
					yAnchor: 1 
			});;

		}
	}
	
}
</script>

<style>
	#map{height:400px;}
	.customoverlay{border:0; box-shadow:0px 1px 2px #888;}
	.customoverlay a {display:block;text-decoration:none;color:#000;text-align:center;border-radius:6px;font-size:14px;font-weight:bold;overflow:hidden;background: #d95050;background: #d95050 url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/arrow_white.png) no-repeat right 14px center;}
	.customoverlay .title {display:block;text-align:center;background:#fff;margin-right:35px;padding:10px 15px;font-size:14px;font-weight:bold;}
	.customoverlay:after {content:'';position:absolute;margin-left:-12px;left:50%;bottom:-12px;width:22px;height:12px;background:url('https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png')}
</style>