<template>
	<v-row>
		<v-col>
			<v-sheet
				min-height="70vh"
				rounded="lg"
			>
				<h2 class="text-center py-9 display-2 font-weight-bold">
					<span v-if="this.$route.name != '예약하기'"><strong class="display-2 blue--text font-weight-bold">{{ nameInfo }}</strong>님의</span>
					{{ this.$route.name }}
				</h2>
				
				<v-form
					ref="form"
					v-model="valid"
					lazy-validation
				>
					<v-select
						label="객실"
						v-model="roomInfo"
						:items="roomSelectItems"
						:readonly="editMode <= 0"
						required
						:rules="roomRules"
					></v-select>
					
					<v-text-field
						v-model="dateRangeText"
						label="입실일&퇴실일"
						readonly
						v-if="!editMode"
					></v-text-field>
					
					<v-dialog
						ref="dialog"
						v-model="modal"
						persistent
						width="290px"
						v-if="editMode"
					>
						<template v-slot:activator="{ on, attrs }">
							<v-text-field
								v-model="dateRangeText"
								label="입실일&퇴실일"
								v-bind="attrs"
								v-on="on"
								required
								:rules="dateRules"
							></v-text-field>
						</template>
						<v-date-picker
							v-model="datesInfo"
							range
							disable
						>
							<v-spacer></v-spacer>
							<v-btn
								text
								color="primary"
								@click="modal = false"
							>
								Cancel
							</v-btn>
							<v-btn
								text
								color="primary"
								@click="$refs.dialog.save(datesInfo)"
							>
								OK
							</v-btn>
						</v-date-picker>
					</v-dialog>
					
					<v-text-field
						label="예약자명"
						v-model="nameInfo"
						:readonly="editMode < 1"
						required
						:rules="nameRules"
					></v-text-field>

					<v-text-field
						label="연락처"
						v-model="phoneInfo"
						:readonly="editMode < 1"
						required
						:rules="telRules"
					></v-text-field>

					<v-row>
						<v-col
							cols="12"
							sm="6"
						>
							<v-select
								label="추가인원"
								v-model="addInfo"
								:items="countSelectItems"
								:readonly="editMode < 1"
								required
								:rules="addRules"
							></v-select>
						</v-col>
						<v-col
							cols="12"
							sm="6"
						>
							<v-select
								label="수영장 온수"
								v-model="poolInfo"
								:items="poolSelectItems"
								:readonly="editMode < 1"
								required
								:rules="poolRules" 
							></v-select>
						</v-col>
					</v-row>

					<v-text-field
            v-model="passwordInfo"
            :rules="passwordRule"
            :type="'password'"
            label="비밀번호"
						v-if="this.$route.name !='예약상세'"
          ></v-text-field>

				</v-form>
				
				<v-row
					justify="center"
					class="py-9"
					v-if="this.$route.name == '예약상세'"
  			>
					<v-btn
						depressed
						large
						class="mx-1 white--text"
						color="teal"
						:to="{name:'예약수정', query:{id:id}}"
						
					>
						<v-icon left>
							mdi-pencil
						</v-icon>
						예약 내역 수정</v-btn>
					
					<v-btn
						depressed
						large
						to="/reserve"
						class="mx-1 white--text"
						color="blue-grey"
					>
						목록</v-btn>
				</v-row>

				<v-row
					justify="center"
					class="py-9"
					v-else
  			>
					
					<v-btn
						depressed
						large
						class="mx-1"
						color="primary"
						@click="validate"
					>
						<v-icon left>
							mdi-pencil
						</v-icon>
						확인</v-btn>
					<v-btn
						depressed
						large
						to="/reserve"
						class="mx-1 white--text"
						color="blue-grey"
					>
						취소</v-btn>	
				</v-row>

				<v-snackbar
					v-model="snackbar"
					:timeout="1000"
				>
					비밀번호를 다시 확인해주세요

				</v-snackbar>
				
			</v-sheet>
		</v-col>
	</v-row>
</template>

<script>
import data from '@/assets/data/reserve.js';
export default {
  name: 'reservedetail',
	components: {
		
	},
	data () {
		return {
			//items:{예약자명:""},
			items:null,
			id:"",
			valid: true,
			editMode:1,
			modal: false,
			snackbar: false,
			
			datesInfo:[],
			nameInfo:"",
			roomInfo:"",
			addInfo:"",
			phoneInfo:"",
			poolInfo:"",
			passwordInfo:"",
			countSelectItems:['1명', '2명', '3명'],
			roomSelectItems :['ROOM 하나', 'ROOM 둘', 'ROOM 셋'],
			poolSelectItems:['예', '아니오'],
			
			roomRules: [
        v => !!v || '객실을 선택해주세요'
      ],
			dateRules: [
        v => !!v || '입퇴실일을 선택해주세요'
      ],
			nameRules: [
        v => !!v || '예약자명을 입력해주세요'
      ],
			telRules: [
        v => !!v || '연락처를 입력해주세요'
      ],
			addRules: [
        v => !!v || '추가인원을 선택해주세요'
      ],
			poolRules: [
        v => !!v || '수영장 온수를 선택해주세요'
      ],
			passwordRule:[
				v => !!v || '비밀번호를 입력해주세요.',
				v => {
					const replaceV = v.replace(/(\s*)/g, '')
					return replaceV.length >= 4 || '4자리 이상 입력해주세요.'
				}
			]
		}
	},
	created(){
		if(this.$dbBoo) this.dbConnect();
		
	},
	methods: {
		async dbConnect() {
			this.$db.collection('Reserve').where('id', '==', this.$route.query.id).get().then((consult)=>{
				this.items = consult.docs.map(v => {
					const item = v.data()
					return {
						id: item.id,
						객실: item.객실,
						입퇴실일: item.입퇴실일,
						예약자명: item.예약자명,
						연락처: item.연락처,
						추가인원: item.추가인원,
						수영장온수: item.수영장온수,
						비밀번호: item.비밀번호,
					}
					
				})
				this.setData()
			})
			
		},
		validate () {
			if (this.$refs.form.validate()) {
        //console.log(this.roomInfo, this.datesInfo, this.nameInfo, this.phoneInfo, this.addInfo, this.poolInfo, this.passwordInfo);
				if(this.$route.name == "예약하기"){
					this.$db.collection('Reserve').get().then(snap => {
						this.$db.collection('Reserve').dot(snap.size).set({
							id: snap.size,
							객실: this.roomInfo,
							입퇴실일: this.datesInfo,
							예약자명: this.nameInfo,
							연락처: this.phoneInfo,
							추가인원: this.addInfo,
							수영장온수: this.poolInfo,
							비밀번호: this.passwordInfo
						});
					});
					
				}else{
					//console.log(this.passwordInfo, this.items[this.id].비밀번호, this.items, this.id)
					if( this.passwordInfo != this.items[0].비밀번호) this.snackbar=true;
					else{
						if(this.$dbBoo){
							
							this.$db.collection('Reserve').doc(String(this.id)).update({
								객실: this.roomInfo,
								입퇴실일: this.datesInfo,
								예약자명: this.nameInfo,
								연락처: this.phoneInfo,
								추가인원: this.addInfo,
								수영장온수: this.poolInfo
							});
						}
					}
				}
				
      }
		},
		setData(){
			this.id = this.$route.query.id;
			this.editMode = 1;
			
			if(this.$route.name != "예약하기"){
				if(this.$route.name =="예약상세") this.editMode = 0;
				if(!this.$dbBoo) this.items = data[this.id];
				//console.log(this.items[0])
				this.roomInfo = this.items[0].객실;
				this.datesInfo = this.items[0].입퇴실일;
				this.nameInfo = this.items[0].예약자명;
				this.phoneInfo = this.items[0].연락처;
				this.addInfo = this.items[0].추가인원;
				this.poolInfo =  this.items[0].수영장온수;
			}
		}
	},
	watch:{
		'$route.path': function(val, oldVal){
			this.setData()
		}
	},
	mounted() {
		//this.setData()
	},
	computed: {
		dateRangeText () {
			return this.datesInfo.join(' ~ ')
		},
	},
}
</script>

