import Vue from 'vue'
import VueRouter 					from 'vue-router'

import HomeView 					from '@/views/HomeView'
import RoomsListView 			from '@/views/rooms/RoomsListView'
import RoomsDetailView		from '@/views/rooms/RoomsDetailView'
import ReserveListView 		from '@/views/reserve/ReserveListView'
import ReserveDetailView 	from '@/views/reserve/ReserveDetailView'
import ContactView 				from '@/views/contact/ContactView'

import DefaultLayout 			from '@/layouts/default/Index'

Vue.use(VueRouter)

const routes = [
	{
    path: '/',
    component: DefaultLayout,
		children:[
			{
				path: '/',
				name: '홈',
				component: HomeView
			},
			{
				path: '/rooms',
				name: '객실안내',
				component: RoomsListView
			},
			{
				path: '/rooms/detail/:contentId',
				name: '객실안내 상세',
				component: RoomsDetailView,
				props: true
			},
			{
				path: '/reserve',
				name: '예약목록',
				component: ReserveListView
			},
			{
				path: '/reserve/create',
				name: '예약하기',
				component: ReserveDetailView,
				props: true
			},
			{
				path: '/reserve/read',
				name: '예약상세',
				component: ReserveDetailView,
				props: true
			},
			{
				path: '/reserve/update',
				name: '예약수정',
				component: ReserveDetailView,
				props: true
			},
			{
				path: '/contact',
				name: '펜션소개',
				component: ContactView
			},
		]
  },
  
  
	
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
