<template>
  
	<v-row>
		<v-col>
			<v-sheet>
				
				<v-carousel cycle>
					<v-carousel-item
						v-for="(item,i) in items"
						:key="i"
						:src="item.src"
					>
						<v-row
							class="fill-height"
							align="center"
							justify="center"
						>
							<div class="text-h2 white--text font-weight-bold">
								{{ item.이름 }}
							</div>
						</v-row>
					</v-carousel-item>
				</v-carousel>

			</v-sheet>
		</v-col>
	</v-row>

</template>

<script>
	import data from '@/assets/data/rooms.js'
  export default {
		name:"HomeView",
    data () {
			return {
				onboarding: 0,
				items: null
			}
		},
		created(){
			if(this.$dbBoo) this.dbConnect();
			else this.items = data;
		},

    methods: {
			async dbConnect() {
				this.$db.collection('Rooms').get().then((consult)=>{
					this.items = consult.docs.map(v => {
						const item = v.data()
						return {
							이름: item.이름,
							특이사항: item.특이사항,
							넓이: item.넓이,
							유형: item.유형,
							기준인원: item.기준인원,
							가격: item.가격,
							src: item.src,
						}
					})
				})
				
			},
    },
  }
</script>

<style scoped>
	.slide_text{text-shadow:5px 5px 5px rgba(0,0,0,.3);}
</style>