<template>
	<v-row>
		<SubMenu v-bind:items="items" />

		<v-col>
			<v-sheet>
				<h2 v-if="items" class="text-center py-9 display-2 font-weight-bold">
					{{ items[this.$route.params.contentId-1].이름 }}
				</h2>
				
				<v-list v-if="items" three-line style="border-top:1px solid #e1e1e1">
					<template v-for="(value, name, index) in items[this.$route.params.contentId-1]">
						<v-list-item :key="index" style="border-bottom:1px solid #e1e1e1">
							<v-list-item-content>
								<v-list-item-title v-if="name != 'src'">
									<strong >{{ name }}</strong>
								</v-list-item-title>
								<v-list-item-subtitle>
									<div v-if="name == 'src'" class="text-center">
										<v-img class="d-inline-block"
											max-height="250"
											max-width="400"
											:src="value"
										></v-img>
									</div>
									<span v-else>{{ value }}</span>
								</v-list-item-subtitle>
							</v-list-item-content>
						</v-list-item>
					</template>
				</v-list>
				
				<v-row
					justify="center"
					class="py-9"
  			>
					<!-- <v-btn
						depressed
						large
						:to="{name:'예약목록 상세', params:{'contentId':this.$route.params.contentId}}"
						class="mx-1"
						color="primary"
					>
						<v-icon left>
							mdi-pencil
						</v-icon>
						예약하기</v-btn>	 -->
					<v-btn
						depressed
						large
						to="/rooms"
						class="mx-1 white--text"
						color="blue-grey"
					>
						<v-icon>fas fa-list</v-icon>
						목록</v-btn>	
				</v-row>
				
				
				
				
			</v-sheet>
		</v-col>
	</v-row>
</template>

<script>
import data from '@/assets/data/rooms.js';
import SubMenu from '@/components/inc/SubMenu.vue';

	
export default {
	components: {
		SubMenu
	},
	data () {
		return {
			items: null,
		}
	},
	created(){
		if(this.$dbBoo) this.dbConnect();
		else this.items = data;
	},
	methods: {
		async dbConnect() {
			
			this.$db.collection('Rooms').get().then((consult)=>{
				this.items = consult.docs.map(v => {
					const item = v.data()
					
					return {
						이름: item.이름,
						특이사항: item.특이사항,
						넓이: item.넓이,
						유형: item.유형,
						기준인원: item.기준인원,
						가격: item.가격,
						src: item.src,
					}
				})
			})
		},
	}
	
}
</script>